<template>
    <div class="update-page">
        <el-form ref="form" :model="form" label-width="80px">
            <div class="header">
                <h3>添加商品</h3>
                <div class="close" @click="close">×</div>
            </div>
            <div class="pagename">
                <el-form-item style="width：100%;" label="单页名称" prop="pagename">
                    {{row.pagename}}
                </el-form-item>
            </div>
            <div class="goods-detail">
                <el-form-item label="商品名称" prop="title" style="float: left;">
                    <el-input style="width: 400px;"  prop="title" ref="title" v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item label="商品链接" prop="links" style="float: left;">
                    <el-input style="width: 400px;"  prop="links" ref="links" v-model="form.links"></el-input>
                </el-form-item>
                <el-button style="width: 100px;float: left;margin-left:10px;" plain type="success" @click="addGoods">添加</el-button>
                <div style="clear:both;"></div>
                <div class="left">
                    <el-table :data="linkslist" style="width: 100%;" border>
                        <el-table-column prop="updateAt" label="日期" width="120"></el-table-column>
                        <el-table-column prop="title" label="标题" width="150"></el-table-column>
                        <el-table-column prop="links" width="500" label="链接"></el-table-column>
                        <el-table-column label="操作" width="300">
                            <template slot-scope="scope">							
                                <el-button size="small" type="primary" @click="updateLink(scope.$index, scope.row)">修改链接</el-button>
                                <el-button size="small" type="danger" style="float:none" @click="deleteLinks(scope.$index, scope.row)">删除链接</el-button>
                            </template>
                        </el-table-column>
                    </el-table>	
                </div>
            </div>
        </el-form>
        <div class="modal" v-if="isUpdateLinksSwitch">
            <update-links v-on:edit="edit" :row="row1"></update-links>
        </div>
    </div>
</template>

<script>
    import store from "../../vuex/store";
    import updateLinks from '../updateLinks/updateLinks'
    import Axios from 'axios';
    export default {
        computed:{
			isUpdateLinksSwitch() {
				return store.state.isUpdateLinksSwitch;
            }
		},
        components: {
            updateLinks
        },
        data() {
            return {
                row: {},
                form: {},
                linkslist: [],
                row1: {}
            }
        },
        mounted() {
            this.row = store.state.goodsData
            this.form.pagename = this.row.pagename
            this.showLinks()
        },
        methods: {
            edit() {
                this.showLinks()
            },
            addGoods() {
                var links = this.$refs.links.$refs.input.value
                var title = this.$refs.title.$refs.input.value
                if(links != '' &&title != '') {
                    Axios({
                        url: "/fetchlink/save_links",
                        method: "POST",
                        withCredentials: false,
                        data: {
                            links: links,
                            class: this.row.class,
                            title: title
                        }
                    }).then(res => {
                        if(res.data.success) {
                            this.$message({
                                type: 'success',
                                message: res.data.success
                            })
                            this.showLinks()
                            Axios({
                                url: "/top10/get_one",
                                method: "POST",
                                withCredentials: false,
                                data: {
                                    key: title,
                                    class: this.row.class,
                                    url: links
                                }
                            }).then(res => {
                                this.$message({
                                    type: 'success',
                                    message: res.data.message
                                })
                                this.form = {}
                            })
                        } else {
                            this.$message.error(res.data.err)
                        }
                    })
                }
            },
            deleteLinks(index, row) {
                Axios({
                    url: "/fetchlink?action=delete_links",
                    method: "get",
                    withCredentials: false,
                    params: {
                        class: row.class,
                        id: row._id
                    }
                }).then(res => {
                    if (res.data.success) {
                        if(res.data.result.length == 1) {
                            this.$message({
                                type: 'success',
                                message: '已删除最后一条数据'
                            })
                            this.linkslist = []
                        } else {
                            this.$message({
                                type: 'success',
                                message: res.data.success
                            })
                            this.showLinks()
                        }
                    }
                })
            },
            updateLink(index, row) {
                this.row1 = row
                store.commit('changeUpdateLinksSwitch', true);
            },
            showLinks() {
                Axios({
                    url: "/fetchlink?action=show_links",
                    method: "get",
                    withCredentials: false,
                    params: {
                        class: this.row.class
                    }
                }).then(res => {
                    if (res.data.success) {
                        let arr = res.data.data
                        for(let i=0;i<arr.length;i++){
                            arr[i].updateAt = arr[i].updateAt.substring(0,10)
                        }
                        this.linkslist = arr
                    } else {
                        this.$message.error(res.data.err)
                    }
                })
            },
            close() {
                this.$router.push('/')
            }
        }
    }
</script>


<style scoped>
.update-page {
    border: 1px solid #eee;
    margin: 50px 0;
}

.header {
    width: 100%;
    border-bottom: 1px solid #eee;
    line-height: 50px;
    overflow: hidden;
}

.header h3 {
    float: left;
    font-weight: 400;
    padding-left: 30px;
}

.close {
    float: right;
    width: 30px;
    height: 30px;
    border: 1px solid #999;
    text-align: center;
    line-height: 30px;
    margin: 9px;
    color: #999;
    cursor: pointer;
}

.pagename {
    height: 50px;
    padding-top: 10px;
    padding-left: 30px;
    border-bottom: 1px solid #eee;
    font-size: 30px;
}

.goods-detail {
    overflow: auto;
    padding: 60px;
}

.left ul{
    width: 1000px;
    padding-top: 50px;
}

.left ul li {
    width: 1000px;
    height: 50px;
    border: 1px solid #eee;
    padding-left: 30px;
    cursor: pointer;
    position: relative;
    margin-bottom: 20px;
}

.left ul li span {
    line-height: 50px;
}

.goods-update {
    width: 100vw;
    height: 100vh;
    background: #000;
    opacity: .9;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.modal {
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .9;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
}
</style>



