<template>
    <div class="update-goods">
        <el-form ref="form" :model="form" label-width="80px">
            <div class="goods-desc">
                <el-form-item :rules="[{ required: true}]" label="图片" prop="imageFile">
                    <el-upload enctype="multipart/form-data" action="http://www.rrdtjj.top/goodsinfo/upload"
                     name="imageFile" :on-success="handleAvatarSuccess">
                        <img v-if="imageUrl" :src="imageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item :rules="[{ required: true, message: '商品名称不能为空'}]" label="商品名称" prop="title">
                    <el-input style="width: 400px;"  prop="title" v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item :rules="[{ required: true, message: '淘口令不能为空'}]" label="淘口令" prop="token">
                    <el-input style="width: 400px;"  prop="token" v-model="form.token"></el-input>
                </el-form-item>
                <div class="submit-btn">
                    <el-button style="width: 150px;" plain type="danger" @click="cancel">取消</el-button>
                    <el-button style="width: 150px;" plain type="success" @click="saveGood">保存</el-button>
                </div>
            </div>
        </el-form>
    </div>
</template>

<script>
    import store from "../../vuex/store";
    import Axios from 'axios';
    export default {
        data() {
            return {
                form: {
                    pagename: '',
                    title: '',
                    token: ''
                },
                imageUrl: '',
                pictUrl: ''
            }
        },
        mounted() {
            this.form = store.state.goodsListData
        },
        methods: {
            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
                this.pictUrl = res.filename
            },
            cancel() {
                store.commit('changeUpdateGoodsSwitch', false)
            },
            saveGood() {
                if(this.pictUrl == '') {
                    this.pictUrl = this.form.pictUrl
                }
                Axios({
                    url: "http://www.rrdtjj.top/goodsinfo?action=update_goods",
                    method: "get",
                    withCredentials: false,
                    params: {
                        class: this.form.class,
                        id: this.form._id,
                        pictUrl: 'http://www.rrdtjj.top/uploads/' + this.pictUrl,
                        title: this.form.title,
                        token: this.form.token,
                    }
                }).then(res => {
                    if (res.data.success) {
                        this.$message({
                            type: 'success',
                            message: '商品信息修改成功'
                        })
                        this.$emit('edit')
                        store.commit('changeUpdateGoodsSwitch', false)
                    } else {
                        this.$message.error(res.data.err);
                    }
                })
                
            }
        }
    }
</script>

<style scoped>
    .update-goods {
        width: 500px;
        height: 350px;
        background: #fff;
        position: fixed;
        top: 50%;
        left: 50%;
        margin: -195px 0 0 -270px;
        padding: 20px;
    }

    .submit-btn {
        text-align: center;
    }

    .avatar {
        width: 100px;
        height: 100px;
    }
</style>




