<template>
    <div class="update-page">
        <el-form ref="form" label-width="80px">
            <div class="header">
                <h3>商品信息展示</h3>
                <div class="close" @click="close">×</div>
            </div>
            <div class="pagename">
                <el-form-item style="width：100%;" label="单页名称" prop="pagename">
                    {{row.pagename}}
                </el-form-item>
            </div>
            <div class="goods-detail">
                <el-table :data="goodslist" style="width: 100%;" border>
                    <el-table-column prop="updateAt" label="日期" width="120"></el-table-column>
                    <el-table-column prop="title" label="标题" width="150"></el-table-column>
                    <el-table-column prop="token" width="150" label="淘口令"></el-table-column>
                    <el-table-column prop="pictUrl" width="477" label="图片地址"></el-table-column>
                    <el-table-column label="操作" width="300">
                        <template slot-scope="scope">							
                            <el-button size="small" type="primary" @click="updateInfo(scope.$index, scope.row)">修改商品信息</el-button>
                            <el-button size="small" type="danger" style="float:none" @click="deleteOne(scope.$index, scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>	
            </div>
            <!-- <div class="search">
                <el-button style="width: 300px;" plain type="success" @click="search">搜索</el-button>
            </div> -->
            <div class="goods-update" v-if="isUpdateGoodsSwitch">
                <update-goods v-on:edit="edit"></update-goods>
            </div>
        </el-form>
    </div>
</template>

<script>
    import store from "../../vuex/store";
    import updateGoods from '../updateGoods/updateGoods'
    import Axios from 'axios';
    export default {
        computed:{
			isUpdateGoodsSwitch() {
				return store.state.isUpdateGoodsSwitch;
            }
		},
        components: {
            updateGoods
        },
        data() {
            return {
                row: {},
                goodslist: [],
            }
        },
        mounted() {
            this.row = store.state.goodsData
            this.showInfo(this.row)
        },
        methods: {
            removeGoods(index) {
                this.goodslist.splice(index, 1)
            },
            edit() {
                this.showInfo(this.row)
                console.log(this.row)
            },
            close() {
                this.$router.push('/')
            },
            updateInfo(index, row) {
                store.state.isUpdateGoodsSwitch = true
                store.commit('setGoodsListData', row)
            },
            deleteOne(index, row) {
                Axios({
                    url: "http://www.rrdtjj.top/goodsinfo?action=delete_one",
                    method: "get",
                    withCredentials: false,
                    params: {
                        class: row.class,
                        id: row._id
                    }
                }).then(res => {
                    if (res.data.success) {
                        if (res.data.result.length == 1) {
                            this.$message({
                                type: 'success',
                                message: '已删除最后一条数据'
                            })
                            this.goodslist = []
                        } else {
                            this.$message({
                                type: 'success',
                                message: res.data.success
                            })
                            this.showInfo(this.row)
                        }
                    }
                })
            },
            showInfo(row) {
                Axios({
                    url: "http://www.rrdtjj.top/goodsinfo?action=show_goods",
                    method: "get",
                    withCredentials: false,
                    params: {
                        class: row.class
                    }
                }).then(res => {
                    if (res.data.success) {
                        let arr = res.data.data
                        for(let i=0;i<arr.length;i++){
                            arr[i].updateAt = arr[i].updateAt.substring(0,10)
                        }
                        this.goodslist = arr
                    } else {
                        // this.$message.error(res.data.err);
                    }
                })
            }
        }
    }
</script>


<style scoped>
.update-page {
    border: 1px solid #eee;
    margin: 50px 0;
}

.header {
    width: 100%;
    border-bottom: 1px solid #eee;
    line-height: 50px;
    overflow: hidden;
}

.header h3 {
    float: left;
    font-weight: 400;
    padding-left: 30px;
}

.close {
    float: right;
    width: 30px;
    height: 30px;
    border: 1px solid #999;
    text-align: center;
    line-height: 30px;
    margin: 9px;
    color: #999;
    cursor: pointer;
}

.pagename {
    height: 50px;
    padding-top: 10px;
    padding-left: 30px;
    border-bottom: 1px solid #eee;
    font-size: 30px;
}

.goods-detail {
    overflow: auto;
}

.left ul{
    width: 1000px;
    padding-top: 50px;
}

.left ul li {
    width: 1000px;
    height: 50px;
    border: 1px solid #eee;
    padding-left: 30px;
    cursor: pointer;
    position: relative;
    margin-bottom: 20px;
}

.left ul li span {
    line-height: 50px;
}

.search {
    text-align: center;
    width: 100%;
    height: 60px;
    line-height: 60px;
    background: #eee;
}

.goods-update {
    width: 100vw;
    height: 100vh;
    background: #000;
    opacity: .9;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}
</style>



