<template>
    <div class="home">
        <div class="add-page">
            <el-button type="primary" plain @click="addPage">新增爆款单页</el-button>
        </div>
        <div class="lists">
            <el-table :data="tableData" style="width: 100%;" border>
				<el-table-column prop="updateAt" label="日期" width="120"></el-table-column>
				<el-table-column prop="pagename" label="单页名称" width="120"></el-table-column>
				<el-table-column prop="visited" width="120" label="浏览人数"></el-table-column>
				<el-table-column prop="copied" width="120" label="复制口令人数"></el-table-column>
				<el-table-column label="操作">
					 <template slot-scope="scope">							
                        <el-button size="small" type="success" :data-clipboard-text="copyTxt + scope.row.class" class="copyBtn" @click="copy">复制链接</el-button>
                        <el-button size="small" type="primary" @click="addGoods(scope.$index, scope.row)">添加商品链接</el-button>
						<el-button size="small" type="danger" style="float:none" @click="editPage(scope.$index, scope.row)">修改单页名称</el-button>
                        <el-button size="small" type="primary" @click="showGoods(scope.$index, scope.row)">商品信息</el-button>
					</template>
				</el-table-column>
			</el-table>	
        </div>
        <div class="modal" v-if="isAddSwitch">
            <page v-on:edit="edit" :str="addStr" :method="addMethod"></page>
        </div>
        <div class="modal" v-if="isEditSwitch">
            <page v-on:edit="edit" :str="editStr" :method="editMethod"></page>
        </div>
    </div>
</template>

<script>
    import store from "../../vuex/store";
    import Page from '../Page/Page.vue'
    import Axios from 'axios'
    import Clipboard from 'clipboard';
    let clipboard = new Clipboard('.copyBtn');
    export default {
        computed:{
			isUpdateSwitch() {
				return store.state.isUpdateSwitch;
            },
            isAddSwitch() {
                return store.state.isAddSwitch;
            },
            isEditSwitch() {
                return store.state.isEditSwitch;
            }
		},
        components: {
            Page,
        },
        data() {
            return {
                tableData: [],
                addStr: '新增',
                editStr: '修改',
                addMethod: 'changeAddSwitch',
                editMethod: 'changeEditSwitch',
                copyTxt: 'http://www.rrdtjj.top/top10.html?class='
            }
        },
        mounted(){
            this.showPage()
        },
        methods: {
            addPage() {
                store.commit("changeAddSwitch",true);
            },
            copy() {
                this.$message({
                    type: 'success',
                    message: '复制成功'
                })
            },
            edit() {
                this.showPage()
            },
            isShow(isShow) {
                this.isModal = isShow
            },
            addGoods(index, row) {
                store.commit("setGoodsData", row);
                this.$router.push('/addLinks')
            },
            showGoods(index, row) {
                store.commit("setGoodsData", row);
                this.$router.push('/showGoods')
            },
            editPage(index, row) {
                store.commit("changeEditSwitch", true);
                store.state.pagesData = row
            },
            showPage() {
                Axios({
                    url: "http://www.rrdtjj.top/show_page",
                    method: "get",
                    withCredentials: false,
                }).then(res => {
                    if (res.data.success) {
                        let arr = res.data.data
                        for(let i=0;i<arr.length;i++){
                            arr[i].updateAt = arr[i].updateAt.substring(0,10)
                        }
                        this.tableData = arr
                    } else {
                        this.$message.error(res.data.err);
                    }
                })
            },
        }
    }
</script>

<style scoped>
.home {
    padding: 100px;
}

.add-page {
    margin-bottom: 20px;
}

/* .lists {
    border: 1px solid #eee;
} */

.modal {
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .9;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
}
</style>



