<template>
    <div class="add">
        <el-form ref="form" :model="form" label-width="80px">
            <div class="header">
                <h3>{{str}}爆款单页</h3>
                <div class="close" @click="close">×</div>
            </div>
            <div class="page">
                <el-form-item style="width：100%;" :rules="[{ required: true, message: '单页名称不能为空'}]" label="单页名称" prop="pagename">
                    <el-input style="width: 300px;float: none;" v-model="form.pagename"></el-input>
                </el-form-item>
                <el-form-item style="width：100%;" :rules="[{ required: true, message: 'class不能为空'}]" label="class" prop="class">
                    <el-input style="width: 300px;float: none;" v-model="form.class"></el-input>
                </el-form-item>
            </div>
            <div class="save">
                <el-button style="width: 150px;" plain type="success" @click="save">保存</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    import store from "../../vuex/store";
    import Axios from 'axios'
    export default {
        props: {
            str: String,
            method: String,
        },
        data() {
            return {
                form: {
                    updateAt: '',
                    pagename: '',
                    class: ''
                }
            }
        },
        mounted() {
            this.form = store.state.pagesData
        },
        methods: {
            close() {
                store.commit(this.method, false);
            },
            save() {
                let addState = store.state.isAddSwitch
                let editState = store.state.isEditSwitch
                if(addState == true && editState == false) {
                    if (this.form.pagename && this.form.class) {
                        Axios({
                            url: "/add_page",
                            method: "get",
                            withCredentials: false,
                            params: {
                                pagename: this.form.pagename,
                                class: this.form.class
                            }
                        }).then(res => {
                            if (res.data.success) {
                                this.$message({
                                    type: "success",
                                    message: res.data.success
                                });
                                this.$emit('edit')
                                store.commit(this.method, false);
                            } else {
                                this.$message.error(res.data.err);
                            }
                        })
                    } else {
                        this.$message.error('输入有误，请重新输入');
                    }
                }
                if (addState == false && editState == true) {
                    if (this.form.pagename && this.form.class) {
                        Axios({
                            url: "/update_page",
                            method: "get",
                            withCredentials: false,
                            params: {
                                id: this.form._id,
                                pagename: this.form.pagename,
                                class: this.form.class
                            },
                        }).then(res => {
                            if (res.data.success) {
                                this.$message({
                                    type: "success",
                                    message: res.data.success
                                });
                                this.$emit('edit')
                                store.commit(this.method, false);
                                console.log(this.method)
                            }
                        })
                    } else {
                        this.$message.error('输入有误，请重新输入');
                    }
                }
                
            }
        }

    }
</script>

<style scoped>
.add {
    width: 500px;
    height: 400px;
    position: fixed;
    top: 50%;
    left: 50%;
    margin: -200px 0 0 -250px;
    background: #eee;
}

.header {
    width: 100%;
    border-bottom: 1px solid #eee;
    line-height: 50px;
    overflow: hidden;
}

.header h3 {
    float: left;
    font-weight: 400;
    padding-left: 30px;
}

.close {
    float: right;
    width: 30px;
    height: 30px;
    border: 1px solid #999;
    text-align: center;
    line-height: 30px;
    margin: 9px;
    color: #999;
    cursor: pointer;
}

.page {
    height: 60px;
    padding-top: 10px;
    padding-left: 30px;
    border-bottom: 1px solid #eee;
}

.save {
    position: absolute;
    bottom: 30px;
    left: 160px;
}
</style>



