<template>
    <div class="update">
        <el-form ref="form" :model="form" label-width="80px">
            <div class="header">
                <h3>链接修改</h3>
                <div class="close" @click="close">×</div>
            </div>
            <div class="page">
                <el-form-item style="width：100%;" label="标题" prop="title">
                    <el-input style="width: 300px;float: none;" v-model="form.title"></el-input>
                </el-form-item>
                <el-form-item style="width：100%;" label="链接" prop="links">
                    <el-input style="width: 300px;float: none;" v-model="form.links"></el-input>
                </el-form-item>
            </div>
            <div class="save">
                <el-button style="width: 150px;" plain type="success" @click="save">保存</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    import store from "../../vuex/store";
    import Axios from 'axios'
    export default {
        props: {
            row: Object
        },
        data() {
            return {
                form: {
                    updateAt: '',
                    title: '',
                    links: ''
                }
            }
        },
        mounted() {
            this.form = this.row
        },
        methods: {
            close() {
                store.commit('changeUpdateLinksSwitch', false);
            },
            save() {
                console.log(this.row)
                if (this.form.title && this.form.links) {
                    Axios({
                        url: "/fetchlink?action=edit_links",
                        method: "get",
                        withCredentials: false,
                        params: {
                            title: this.form.title,
                            links: this.form.links,
                            id: this.row._id,
                            class: this.row.class
                        }
                    }).then(res => {
                        if (res.data.success) {
                            this.$message({
                                type: "success",
                                message: res.data.success
                            });
                            Axios({
                                url: "/top10/get_one",
                                method: "POST",
                                withCredentials: false,
                                data: {
                                    key: this.form.title,
                                    class: this.row.class,
                                    url: this.form.links
                                }
                            }).then(res => {
                                this.$message({
                                    type: 'success',
                                    message: res.data.message
                                })
                                this.form = {}
                            })
                            this.$emit('edit')
                            store.commit('changeUpdateLinksSwitch', false);
                        } else {
                            this.$message.error(res.data.err);
                        }
                    })
                } else {
                    this.$message.error('输入有误，请重新输入');
                }
            }
        }

    }
</script>

<style scoped>
.update {
    width: 500px;
    height: 400px;
    position: fixed;
    top: 50%;
    left: 50%;
    margin: -200px 0 0 -250px;
    background: #eee;
}

.header {
    width: 100%;
    border-bottom: 1px solid #eee;
    line-height: 50px;
    overflow: hidden;
}

.header h3 {
    float: left;
    font-weight: 400;
    padding-left: 30px;
}

.close {
    float: right;
    width: 30px;
    height: 30px;
    border: 1px solid #999;
    text-align: center;
    line-height: 30px;
    margin: 9px;
    color: #999;
    cursor: pointer;
}

.page {
    height: 60px;
    padding-top: 10px;
    padding-left: 30px;
    border-bottom: 1px solid #eee;
}

.save {
    position: absolute;
    bottom: 30px;
    left: 160px;
}
</style>



