import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
var state={
	isAddSwitch: false,
	isEditSwitch: false,
	isUpdateLinksSwitch: false,
	goodsData: {},
	pagesData: {
		pagename: '请输入单页名称',
		class: '请输入class'
	},
	goodsListData: {},
	isUpdateGoodsSwitch: false
};
var mutations={
	changeAddSwitch: function(state, data) {
        state.isAddSwitch = data;
	},
	changeEditSwitch: function(state, data) {
        state.isEditSwitch = data;
	},
	changeUpdateLinksSwitch: function(state, data) {
        state.isUpdateLinksSwitch = data;
	},
	changeUpdateGoodsSwitch: function(state, data) {
        state.isUpdateGoodsSwitch = data;
	},
	setGoodsData(state, data) {
		if (data) {
			state.goodsData = data
        } else {
            state.goodsData = {}
        }
	},
	setPagesData(state, data) {
		if (data) {
			state.pagesData = data
        } else {
            state.pagesData = {}
        }
	},
	setGoodsListData(state, data) {
		if (data) {
			state.goodsListData = data
        } else {
            state.goodsListData = {}
        }
	}
};
export default new Vuex.Store({
	state,
	mutations
})



