import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home/Home'
import addLinks from '@/components/addLinks/addLinks'
import showGoods from '@/components/showGoods/showGoods'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Home,
      meta: {
        title: '首页'
      }
    },
    {
      path: '/addLinks',
      component: addLinks,
      meta: {
        title: '添加商品链接'
      }
    },
    {
      path: '/showGoods',
      component: showGoods,
      meta: {
        title: '详细商品信息'
      }
    }
  ]
})



